.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-around;
  width: 200px;
  margin: 0;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  height: 14px;
  margin-bottom: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.card__review-count {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  height: 18px;
  margin-bottom: 16px;
  display: flex;
}

.card__price .size {
  font-style: bold;
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price .style {
  align-content: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars .stars__star:last-child {
  margin-right: 0;
}

:root {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

body {
  margin: 0;
}
